.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #6200EE;
  padding: 1em;
  margin-bottom: 20px;
}

.list {
  display: flex;
  list-style: none;
  align-items: center;
}

.item {
  margin-right: 1em;
}

.item a {
  color: #FFF;
  text-decoration: none;
}

.item a:hover {
  color: #F9AB55;
}